import React from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  CCreateElement,
  CSidebar,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
} from "@coreui/react";

// sidebar nav config
import navigation from "./_nav";
import navExam from "./navbar/nav_exam";
import nav_Scholarship from "./navbar/nav_Scholarship";
import nav_dept from "./navbar/nav_dept";
import nav_Admission from "./navbar/nav_Admission";
import nav_naac from "./navbar/nav_naac";
const TheSidebar = () => {
  const dispatch = useDispatch();
  const show = useSelector((state) => state.sidebarShow);
  const user_roll = useSelector((state) => state.user_roll);
  let nav;

  if (user_roll == "4") {
    nav = navExam;
  } else if (user_roll == "6") {
    nav = nav_Scholarship;
  } else if (user_roll == "2") {
    nav = navigation;
  } else if (user_roll == "3") {
    nav = nav_Admission;
  } else if (user_roll == "5") {
    nav = nav_naac;
  } else if (user_roll == "1") {
    nav = nav_dept;
  } else {
    nav = null;
  }
  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({ type: "set", sidebarShow: val })}
      colorScheme="light"
      aside={true}
    >
      <CSidebarNav>
        <CCreateElement
          items={nav}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle,
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none" />
    </CSidebar>
  );
};

export default React.memo(TheSidebar);
